import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state = {
    language: 'en',
    textToTranslate: '',
    translatedText:''
}
const mutations = {
    updatelanguage(state, language) {
        state.language = language;
    },
    updatetextToTranslate(state, textToTranslate) {
        state.textToTranslate = textToTranslate;
    },
    updateTranslatedText(state,translatedText){
        state.translatedText = translatedText
    }
}

export default new Vuex.Store({
    state,
    mutations
})


