<template>
  <div id="app">
    <img src="./assets/logo.png">
<!--     <router-view/> -->
    <trans-form @submittedText='translate' />
    <trans-result />
  </div>
</template>

<script>
import Transform from './components/Transform'
import Transresult from './components/Transresult'
import store from "./store/store.js";
import { mapState } from "vuex";

export default {
  name: 'App',
  store,
  components:{
    'trans-form':Transform,
    'trans-result':Transresult
  },
  methods:{
    translate:function(){
      console.log(this.$store.state.language);
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180925T090740Z.264f47de24d3e157.9b3ef71b0822e6346882204280028b36ffec94f3&lang='+ this.$store.state.language +'&text=' + this.$store.state.textToTranslate).then((response) =>{this.$store.commit('updateTranslatedText',response.body.text[0])});
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>


