<template>
  <div id="transresult">
      <p class="title">翻译结果：</p>
      <p class="result">{{$store.state.translatedText}}</p>
  </div>
</template>

<script>
import store from "@/store/store.js";

export default {
  name: 'HelloWorld',
  store
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#transresult{
    border: 0px solid #000;
    margin: 30px auto;
    width: 800px;
    height: 300px;
}
#transresult .title{
    font-family: 'Microsoft JhengHei';
    font-size: 28px;
}
#transresult .result{
    font-size: 24px;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>


