<template>
  <div id="transform">
      <h1 class="title">Vue<span>翻译网站</span></h1>
      <p class="subtitle">vuex版</p>
      <form >
          <input type="text" placeholder="请输入翻译的文本" :value="textToTranslate" @input="updatetextToTranslate">
          <select :value="language" @input="updatelanguage">
              <option value="en">English</option>
              <option value="ru">Russia</option>
              <option value="ko">Korean</option>
              <option value="ja">Japanese</option>
          </select>
          <input type="submit" value="翻译" @click="submitText">
      </form>
<!--       <p>{{this.language}}</p>
      <p>{{this.textToTranslate}}</p> -->
  </div>
</template>

<script>
import store from "@/store/store.js";
import { mapState } from "vuex";
export default {
  name: "Transform",
  store,
  computed: {
    ...mapState({
      language: state => state.language,
      textToTranslate: state => state.textToTranslate
    })
    /* submitText(){
          console.log(this.language, this.textToTranslate);
          this.$emit('submittedText',this.language,this.textToTranslate)
      } */
  },
  methods: {
    updatelanguage(e) {
      this.$store.commit("updatelanguage", e.target.value);
    },
    updatetextToTranslate(e) {
      this.$store.commit("updatetextToTranslate", e.target.value);
    },
    submitText(e){
        this.$emit('submittedText');
        e.preventDefault();
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#transform{
    border: 0px solid #000;
}
#transform .title{
    font-size: 60px;
}
#transform .title span{
    letter-spacing: 20px;
    margin-left: 20px;
}
#transform .subtitle{
    font-size: 42px;
    font-weight: 10 !important;
    color: rgb(124, 129, 128);
}
#transform form>:first-child{
    border: 0;
    background-color: rgb(226, 226, 226);
    width: 400px;
    height: 60px;
    padding-left: 15px;
    outline: none;
    border-radius: 8px;
    font-size: 22px;
}
#transform form select{
    width: 120px;
    height: 50px;
    text-align: center;
    padding-left: 15px;
    margin-left: 50px;
    border-radius: 10px;
    outline: none;
    border: 0 solid #000;
    background-color: rgb(173, 222, 255);
    color: rgb(75, 74, 74);
    font-size: 18px
}
#transform form select option{
    background-color: white;
}
#transform form>:last-child{
    display: block;
    margin: 0 auto;
    margin-top: 60px;
    width: 200px;
    height: 80px;
    text-align: center;
    border-radius: 8px;
    outline: none;
    border: 0 solid #000;
    background-color: rgb(255, 245, 212);
    color: rgb(75, 74, 74);
    font-size: 30px;
    cursor: pointer;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



